.floating-btn {
  position: fixed;
  width : 120px;
  height: 38px;
  margin: 10px auto;
  left: 0;
  right: 0;
  bottom: 0
}

.my-info {
  background: url(./assets/info-icon.png) no-repeat;
  background-size: 100%;
  border: none;
  outline: none;
}

.my-money {
  background: url(./assets/money.png) no-repeat;
  background-size: 100%;
  border: none;
  outline: none;
}

.my-winged-money {
  background: url(./assets/winged-money.png) no-repeat;
  background-size: 100%;
  border: none;
  outline: none;
}

.my-bg {
  background-color: #02a8a8; 
}



